import React, {useEffect, useState} from 'react'
import * as s from './ProfileStatus.module.css'
import sError from '../../../common/Forms/forms.module.css'
import {createField, Input} from '../../../common/Forms/Forms'
import {maxLength, required} from '../../../../utils/validators'
import {reduxForm} from 'redux-form'

const maxLengthStatus = maxLength(300)

const StatusForm = ({handleSubmit, error}) => (
    <form onSubmit={handleSubmit}>
        {createField('status', 'Status', [required, maxLengthStatus], Input)}
        {error && <div className={sError.formError}>{error}</div>}
    </form>
)

const StatusFormRedux = reduxForm({form: 'statusForm'})(StatusForm)

const ProfileStatusWithHooks = props => {
    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(props.status)

    const activateEditMode = () => setEditMode(true)
    const deactivateEditMode = () => {
        setEditMode(false)
        props.updateStatus(status)
    }

    const onStatusChange = e => setStatus(e.currentTarget.value)

    return (
        <>
            {!editMode
                ? <div>
                    <span onDoubleClick={activateEditMode}>
                        {status || 'No status'}
                    </span>
                </div>
                : <input
                    onChange={onStatusChange}
                    autoFocus={true}
                    onBlur={deactivateEditMode}
                    value={status}
                />
            }
        </>
    )
}

export default ProfileStatusWithHooks